<template>
  <div class="educate-bar">
    <!--  -->
    <div class="bar-container d-flex jc-center ai-center">
      <!-- menu  -->
      <ul class="m-0 p-0 mt-5">
        <li
          class="bar-item mx-3 fs-lg text-light-grey"
          :class="{'bar-item-active': active === index}"
          @click="changeCard(index)"
          v-for="(item, index) in content"
          :key="index"
        >{{item}}</li>
      </ul>
    </div>
  </div>
</template>

<script>

export default {
  props: ["content"],
  data() {
    return {
      active: 0 //menu
    };
  },
  methods: {
    //    display changeSlide 
      changeCard(index){
          this.active = index;
          this.$emit('parentChangeSlide',index); //
      }
  },
};
</script>

<style lang="scss" scoped>
@import "../../../assets/scss/variables.scss";
// 
.bar-container {
  // menu
  ul {
    // menu item
    .bar-item {
      &:hover {
        color: map-get($colors, "primary");
        transition: all 0.3s;
      }
    }
  }
}
// menu
.bar-item-active {
  color: map-get($colors, "primary");
  transition: all 0.3s;
}
</style>